import { RightsHolderContext } from "./interfaces";
import axiosInstance, { unpack } from "../../infrastructure/axios-config";
import { RightsHolder } from "../../interfaces";

export type { RightsHolderContext } from "./interfaces";

export const rightsHolderApi = {
  getRightsHolders: async () => {
    try {
      const response = await axiosInstance.get<RightsHolder[]>(
        "/RightsHolder/getRightsHolders"
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
    }
  },
  getRightsHolder: async (context: RightsHolderContext) => {
    try {
      const { id } = context;
      const response = await axiosInstance.get<RightsHolder>(
        `/RightsHolder/getRightsHolder/${id}`
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
    }
  },
  createRightsHolder: async () => {
    try {
      const response = await axiosInstance.post<RightsHolder>(
        "/RightsHolder/createRightsHolder"
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
    }
  },
  updateRightsHolder: async (context: RightsHolder) => {
    try {
      await axiosInstance.put("/RightsHolder/updateRightsHolder", context);
    } catch (error) {
      console.error(error);
    }
  },
  deleteRightsHolder: async (context: RightsHolderContext) => {
    try {
      const { id } = context;
      await axiosInstance.delete(`/RightsHolder/deleteRightsHolder/${id}`);
    } catch (error) {
      console.error(error);
    }
  }
};
