import { LicenseActionContext } from "./interfaces";
import axiosInstance, { unpack } from "../../infrastructure/axios-config";
import { LicenseAction } from "../../interfaces";

export type { LicenseActionContext } from "./interfaces";

export const licenseActionApi = {
  getLicenseActions: async () => {
    try {
      const response = await axiosInstance.get<LicenseAction[]>(
        "/LicenseAction/getLicenseActions"
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
    }
  },
  getLicenseAction: async (context: LicenseActionContext) => {
    try {
      const { id } = context;
      const response = await axiosInstance.get<LicenseAction>(
        `/LicenseAction/getLicenseAction/${id}`
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
    }
  },
  createLicenseAction: async () => {
    try {
      const response = await axiosInstance.post<LicenseAction>(
        "/LicenseAction/createLicenseAction"
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
    }
  },
  updateLicenseAction: async (context: LicenseAction) => {
    try {
      await axiosInstance.put("/LicenseAction/updateLicenseAction", context);
    } catch (error) {
      console.error(error);
    }
  },
  deleteLicenseAction: async (context: LicenseActionContext) => {
    try {
      const { id } = context;
      await axiosInstance.delete(`/LicenseAction/deleteLicenseAction/${id}`);
    } catch (error) {
      console.error(error);
    }
  }
};
