import { PriceListContext } from "./interfaces";
import axiosInstance, { unpack } from "../../infrastructure/axios-config";
import { PriceList } from "../../interfaces";

export type { PriceListContext } from "./interfaces";

export const priceListApi = {
  getPriceLists: async () => {
    try {
      const response = await axiosInstance.get<PriceList[]>(
        "/PriceList/getPriceLists"
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
    }
  },
  getPriceList: async (context: PriceListContext) => {
    try {
      const { id } = context;
      const response = await axiosInstance.get<PriceList>(
        `/PriceList/getPriceList/${id}`
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
    }
  },
  createPriceList: async () => {
    try {
      const response = await axiosInstance.post<PriceList>(
        "/PriceList/createPriceList"
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
    }
  },
  updatePriceList: async (context: PriceList) => {
    try {
      const response = await axiosInstance.put<PriceList>(
        "/PriceList/updatePriceList",
        context
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
    }
  },
  deletePriceList: async (context: PriceListContext) => {
    try {
      const { id } = context;
      await axiosInstance.delete(`/PriceList/deletePriceList/${id}`);
    } catch (error) {
      console.error(error);
    }
  }
};
