import { useEffect, useState } from "react";

import { GetRegistrationStatusHeaders } from "./columns";
import { registrationStatusApi } from "../../api/RegistrationStatus";
import { EditableTable } from "../../components/EditableTable/EditableTable";
import { RegistrationStatus } from "../../interfaces";

export const RegistrationStatuses = () => {
  const [registrationStatuss, setRegistrationStatuses] = useState<
    RegistrationStatus[]
  >([]);

  useEffect(() => {
    registrationStatusApi.getRegistrationStatuses().then((data) => {
      if (data) {
        setRegistrationStatuses(data);
      }
    });
  }, []);

  const onAdd = () => {
    registrationStatusApi
      .createRegistrationStatus()
      .then((newRegistrationStatus) => {
        if (newRegistrationStatus) {
          setRegistrationStatuses([
            ...registrationStatuss,
            newRegistrationStatus
          ]);
        }
      });
  };

  const onUpdate = (updatedRegistrationStatuse: RegistrationStatus) => {
    registrationStatusApi
      .updateRegistrationStatus(updatedRegistrationStatuse)
      .then(() => {
        const index = registrationStatuss.findIndex(
          (cc) => cc.id === updatedRegistrationStatuse.id
        );
        if (index !== -1) {
          const updatedDataSource = [...registrationStatuss];
          updatedDataSource.splice(index, 1, updatedRegistrationStatuse);
          setRegistrationStatuses(updatedDataSource);
        }
      });
  };

  const onDelete = (id: number) => {
    registrationStatusApi.deleteRegistrationStatus({ id }).then(() => {
      const index = registrationStatuss.findIndex((cc) => cc.id === id);
      if (index !== -1) {
        const updatedDataSource = [...registrationStatuss];
        updatedDataSource.splice(index, 1);
        setRegistrationStatuses(updatedDataSource);
      }
    });
  };

  return (
    <>
      <EditableTable
        headers={GetRegistrationStatusHeaders()}
        data={registrationStatuss}
        onAdd={onAdd}
        onUpdate={onUpdate}
        onDelete={onDelete}
      />
    </>
  );
};
