import { CorporateCustomerContext } from "./interfaces";
import axiosInstance, { unpack } from "../../infrastructure/axios-config";
import { CorporateCustomer } from "../../interfaces";

export type { CorporateCustomerContext } from "./interfaces";

export const corporateCustomerApi = {
  getCorporateCustomers: async () => {
    try {
      const response = await axiosInstance.get<CorporateCustomer[]>(
        "/CorporateCustomer/getCorporateCustomers"
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
    }
  },
  getCorporateCustomer: async (context: CorporateCustomerContext) => {
    try {
      const { id } = context;
      const response = await axiosInstance.get<CorporateCustomer>(
        `/CorporateCustomer/getCorporateCustomer/${id}`
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
    }
  },
  createCorporateCustomer: async () => {
    try {
      const response = await axiosInstance.post<CorporateCustomer>(
        "/CorporateCustomer/createCorporateCustomer"
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
    }
  },
  updateCorporateCustomer: async (context: CorporateCustomer) => {
    try {
      await axiosInstance.put(
        "/CorporateCustomer/updateCorporateCustomer",
        context
      );
    } catch (error) {
      console.error(error);
    }
  },
  deleteCorporateCustomer: async (context: CorporateCustomerContext) => {
    try {
      const { id } = context;
      await axiosInstance.delete(
        `/CorporateCustomer/deleteCorporateCustomer/${id}`
      );
    } catch (error) {
      console.error(error);
    }
  }
};
