import { useEffect, useState } from "react";

import { GetCustomersHeaders } from "./columns";
import { corporateCustomerApi } from "../../api/CorporateCustomer";
import { customerApi } from "../../api/Customer";
import { EditableTable } from "../../components/EditableTable/EditableTable";
import { Customer } from "../../interfaces";

export const Customers = () => {
  const [customers, setCustomers] = useState<Customer[]>([]);
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  const [extraData, setExtraData] = useState<any>();

  useEffect(() => {
    customerApi.getCustomers().then((data) => {
      if (data) {
        setCustomers(data);
      }
    });
    corporateCustomerApi.getCorporateCustomers().then((data) => {
      if (data) {
        setExtraData({ corporateCustomers: data });
      }
    });
  }, []);

  const onAdd = () => {
    customerApi.createCustomer().then((newCustomer) => {
      if (newCustomer) {
        setCustomers([...customers, newCustomer]);
      }
    });
  };

  const onUpdate = (updatedCustomer: Customer) => {
    /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
    customerApi.updateCustomer(updatedCustomer).then((data: any) => {
      const updatedCustomers = [...customers].map((el) => {
        if (el.id !== updatedCustomer.id) {
          return el;
        } else {
          return data;
        }
      });
      setCustomers(updatedCustomers);
      // const index = customers.findIndex((cc) => cc.id === updatedCustomer.id);
      // if (index !== -1 && data) {
      //   const updatedDataSource = [...customers];
      //   updatedDataSource.splice(index, 1, data);
      //   setCustomers(updatedDataSource);
      // }
    });
  };

  const onDelete = (id: number) => {
    customerApi.deleteCustomer({ id }).then(() => {
      const index = customers.findIndex((cc) => cc.id === id);
      if (index !== -1) {
        const updatedDataSource = [...customers];
        updatedDataSource.splice(index, 1);
        setCustomers(updatedDataSource);
      }
    });
  };

  return (
    <>
      <EditableTable
        headers={GetCustomersHeaders()}
        data={customers}
        onAdd={onAdd}
        onUpdate={onUpdate}
        onDelete={onDelete}
        extraData={extraData}
      />
    </>
  );
};
