import { LicenseTransactionContext } from "./interfaces";
import axiosInstance, { unpack } from "../../infrastructure/axios-config";
import { LicenseTransaction } from "../../interfaces";

export type { LicenseTransactionContext } from "./interfaces";

export const licenseTransactionApi = {
  getLicenseTransactions: async () => {
    try {
      const response = await axiosInstance.get<LicenseTransaction[]>(
        "/LicenseTransaction/getLicenseTransactions"
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
    }
  },
  getLicenseTransaction: async (context: LicenseTransactionContext) => {
    try {
      const { id } = context;
      const response = await axiosInstance.get<LicenseTransaction>(
        `/LicenseTransaction/getLicenseTransaction/${id}`
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
    }
  },
  createLicenseTransaction: async () => {
    try {
      const response = await axiosInstance.post<LicenseTransaction>(
        "/LicenseTransaction/createLicenseTransaction"
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
    }
  },
  updateLicenseTransaction: async (context: LicenseTransaction) => {
    try {
      const response = await axiosInstance.put<LicenseTransaction>(
        "/LicenseTransaction/updateLicenseTransaction",
        context
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
    }
  },
  deleteLicenseTransaction: async (context: LicenseTransactionContext) => {
    try {
      const { id } = context;
      await axiosInstance.delete(
        `/LicenseTransaction/deleteLicenseTransaction/${id}`
      );
    } catch (error) {
      console.error(error);
    }
  }
};
