import { useEffect, useState } from "react";

import { GetContractHeaders } from "./columns";
import { contractApi, customerApi, rightsHolderApi } from "../../api";
import { EditableTable } from "../../components/EditableTable/EditableTable";
import { Contract } from "../../interfaces";

export const Contracts = () => {
  const [contracts, setContracts] = useState<Contract[]>([]);
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  const [extraData, setExtraData] = useState<any>();

  useEffect(() => {
    contractApi.getContracts().then((data) => {
      if (data) {
        setContracts(data);
      }
    });
    customerApi.getCustomers().then((customers) => {
      rightsHolderApi.getRightsHolders().then((rightsHolders) => {
        setExtraData({ customers, rightsHolders });
      });
    });
  }, []);

  const onAdd = () => {
    contractApi.createContract().then((newContract) => {
      if (newContract) {
        setContracts([...contracts, newContract]);
      }
    });
  };

  const onUpdate = (updatedContract: Contract) => {
    /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
    contractApi.updateContract(updatedContract).then((data: any) => {
      const updatedContracts = [...contracts].map((el) => {
        if (el.id !== updatedContract.id) {
          return el;
        } else {
          return data;
        }
      });
      setContracts(updatedContracts);
      // const index = contracts.findIndex((cc) => cc.id === updatedContract.id);
      // if (index !== -1 && data) {
      //   const updatedDataSource = [...contracts];
      //   updatedDataSource.splice(index, 1, data);
      //   setContracts(updatedDataSource);
      // }
    });
  };

  const onDelete = (id: number) => {
    contractApi.deleteContract({ id }).then(() => {
      const index = contracts.findIndex((cc) => cc.id === id);
      if (index !== -1) {
        const updatedDataSource = [...contracts];
        updatedDataSource.splice(index, 1);
        setContracts(updatedDataSource);
      }
    });
  };

  return (
    <>
      <EditableTable
        headers={GetContractHeaders()}
        data={contracts}
        onAdd={onAdd}
        onUpdate={onUpdate}
        onDelete={onDelete}
        extraData={extraData}
      />
    </>
  );
};
