import { useEffect, useState } from "react";

import { GetProductFamilyHeaders } from "./columns";
import { productFamilyApi } from "../../api/ProductFamily";
import { EditableTable } from "../../components/EditableTable/EditableTable";
import { ProductFamily } from "../../interfaces";

export const ProductFamilies = () => {
  const [productFamilies, setProductFamilies] = useState<ProductFamily[]>([]);

  useEffect(() => {
    productFamilyApi.getProductFamilies().then((data) => {
      if (data) {
        setProductFamilies(data);
      }
    });
  }, []);

  const onAdd = () => {
    productFamilyApi.createProductFamily().then((newProductFamily) => {
      if (newProductFamily) {
        setProductFamilies([...productFamilies, newProductFamily]);
      }
    });
  };

  const onUpdate = (updatedProductFamily: ProductFamily) => {
    productFamilyApi.updateProductFamily(updatedProductFamily).then(() => {
      const index = productFamilies.findIndex(
        (cc) => cc.id === updatedProductFamily.id
      );
      if (index !== -1) {
        const updatedDataSource = [...productFamilies];
        updatedDataSource.splice(index, 1, updatedProductFamily);
        setProductFamilies(updatedDataSource);
      }
    });
  };

  const onDelete = (id: number) => {
    productFamilyApi.deleteProductFamily({ id }).then(() => {
      const index = productFamilies.findIndex((cc) => cc.id === id);
      if (index !== -1) {
        const updatedDataSource = [...productFamilies];
        updatedDataSource.splice(index, 1);
        setProductFamilies(updatedDataSource);
      }
    });
  };

  return (
    <>
      <EditableTable
        headers={GetProductFamilyHeaders()}
        data={productFamilies}
        onAdd={onAdd}
        onUpdate={onUpdate}
        onDelete={onDelete}
      />
    </>
  );
};
