import { LicenseTypeContext } from "./interfaces";
import axiosInstance, { unpack } from "../../infrastructure/axios-config";
import { LicenseType } from "../../interfaces";

export type { LicenseTypeContext } from "./interfaces";

export const licenseTypeApi = {
  getLicenseTypes: async () => {
    try {
      const response = await axiosInstance.get<LicenseType[]>(
        "/LicenseType/getLicenseTypes"
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
    }
  },
  getLicenseType: async (context: LicenseTypeContext) => {
    try {
      const { id } = context;
      const response = await axiosInstance.get<LicenseType>(
        `/LicenseType/getLicenseType/${id}`
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
    }
  },
  createLicenseType: async () => {
    try {
      const response = await axiosInstance.post<LicenseType>(
        "/LicenseType/createLicenseType"
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
    }
  },
  updateLicenseType: async (context: LicenseType) => {
    try {
      await axiosInstance.put("/LicenseType/updateLicenseType", context);
    } catch (error) {
      console.error(error);
    }
  },
  deleteLicenseType: async (context: LicenseTypeContext) => {
    try {
      const { id } = context;
      await axiosInstance.delete(`/LicenseType/deleteLicenseType/${id}`);
    } catch (error) {
      console.error(error);
    }
  }
};
