import { CustomerContext } from "./interfaces";
import axiosInstance, { unpack } from "../../infrastructure/axios-config";
import { Customer } from "../../interfaces";

export type { CustomerContext } from "./interfaces";

export const customerApi = {
  getCustomers: async () => {
    try {
      const response = await axiosInstance.get<Customer[]>(
        "/Customer/getCustomers"
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
    }
  },
  getCustomer: async (context: CustomerContext) => {
    try {
      const { id } = context;
      const response = await axiosInstance.get<Customer>(
        `/Customer/getCustomer/${id}`
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
    }
  },
  createCustomer: async () => {
    try {
      const response = await axiosInstance.post<Customer>(
        "/Customer/createCustomer"
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
    }
  },
  updateCustomer: async (context: Customer) => {
    try {
      const response = await axiosInstance.put<Customer>(
        "/Customer/updateCustomer",
        context
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
    }
  },
  deleteCustomer: async (context: CustomerContext) => {
    try {
      const { id } = context;
      await axiosInstance.delete(`/Customer/deleteCustomer/${id}`);
    } catch (error) {
      console.error(error);
    }
  }
};
