import { useEffect, useState } from "react";

import { GetSoftwareLifeStagesHeaders } from "./columns";
import { softwareLifeStageApi } from "../../api/SoftwareLifeStage";
import { EditableTable } from "../../components/EditableTable/EditableTable";
import { SoftwareLifeStage } from "../../interfaces";

export const SoftwareLifeStages = () => {
  const [SoftwareLifeStages, setSoftwareLifeStages] = useState<
    SoftwareLifeStage[]
  >([]);

  useEffect(() => {
    softwareLifeStageApi.getSoftwareLifeStages().then((data) => {
      if (data) {
        setSoftwareLifeStages(data);
      }
    });
  }, []);

  const onAdd = () => {
    softwareLifeStageApi
      .createSoftwareLifeStage()
      .then((newSoftwareLifeStage) => {
        if (newSoftwareLifeStage) {
          setSoftwareLifeStages([...SoftwareLifeStages, newSoftwareLifeStage]);
        }
      });
  };

  const onUpdate = (updatedSoftwareLifeStage: SoftwareLifeStage) => {
    softwareLifeStageApi
      .updateSoftwareLifeStage(updatedSoftwareLifeStage)
      .then(() => {
        const index = SoftwareLifeStages.findIndex(
          (cc) => cc.id === updatedSoftwareLifeStage.id
        );
        if (index !== -1) {
          const updatedDataSource = [...SoftwareLifeStages];
          updatedDataSource.splice(index, 1, updatedSoftwareLifeStage);
          setSoftwareLifeStages(updatedDataSource);
        }
      });
  };

  const onDelete = (id: number) => {
    softwareLifeStageApi.deleteSoftwareLifeStage({ id }).then(() => {
      const index = SoftwareLifeStages.findIndex((cc) => cc.id === id);
      if (index !== -1) {
        const updatedDataSource = [...SoftwareLifeStages];
        updatedDataSource.splice(index, 1);
        setSoftwareLifeStages(updatedDataSource);
      }
    });
  };

  return (
    <>
      <EditableTable
        headers={GetSoftwareLifeStagesHeaders()}
        data={SoftwareLifeStages}
        onAdd={onAdd}
        onUpdate={onUpdate}
        onDelete={onDelete}
      />
    </>
  );
};
